@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

$darkblue: #272f48;
$title-size: 86px;

// ScrollBar
html {
  scrollbar-width: thin;
  scrollbar-color: $darkblue $darkblue;
}
body::-webkit-scrollbar {
  width: 13px;
}
body::-webkit-scrollbar-track {
  background: white;
}
body::-webkit-scrollbar-thumb {
  background-color: #8892b0;
  border: 3px solid white;
  border-radius: 10px;
  &:hover {
    background-color: #495670;
  }
}

a {
  text-decoration: none;
  color: $darkblue;
}
.main-container {
  margin-bottom: 100px;
}
.toolbar {
  border-bottom: 1.5px solid $darkblue;
  padding: 20px 50px 20px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: sticky;
  top: 0px;
  background-color: white;
  z-index: 10;
  width: 100%;
  box-sizing: border-box;
  .title {
    cursor: pointer;
    display: inline-flex;
    font-size: 34px;
  }
  .toolbar-info {
    display: inline-flex;
    .toolbar-item {
      cursor: pointer;
      padding-left: 50px;
      font-size: 20px;
    }
  }
}

.container-box {
  display: flex;
  margin: 3% 6% 0px 10%;
  justify-content: space-between;
  align-items: flex-end;
  .background-box {
    border-radius: 55px;
    background: transparent linear-gradient(216deg, #ff5af9 0%, #63b6ff 59%, #4bffff 100%) 0% 0% no-repeat padding-box;
    width: 40vw;
    height: 100vh;
    position: relative;
    .trans-box {
      background: transparent linear-gradient(91deg, #ffffff00 0%, #ffffff29 100%) 0% 0% no-repeat padding-box;
      box-shadow: -19px 20px 14px rgb(0, 0, 0, 0.16);
      border-radius: 57px;
      position: absolute;
      width: 42vw;
      height: 75vh;
      top: 6%;
      left: 16%;
      padding: 8%;
      white-space: pre-wrap;
      .name {
        font-size: $title-size;
      }
      .description {
        margin-top: 4%;
        font-size: 30px;
      }
      .button {
        background: #ffffff 0% 0% no-repeat padding-box;
        border: 5px solid #ff5af95c;
        border-radius: 5px;
        display: inline-block;
        margin-top: 25px;
        padding: 0px 20px 7px 20px;
        align-self: center;
        font-size: 36px;
        border-radius: 500px;
        font-style: italic;
      }
      .icon-container {
        margin-top: 25px;
        display: flex;
        .icon {
          margin-right: 20px;
        }
      }
    }
  }
  .image-box {
    width: 900px;
    height: 100vh;
    position: relative;
    z-index: -1;
    margin-left: 3%;
    .background {
      max-width: 90%;
      height: auto;
      position: absolute;
      bottom: 0;
    }
    .people {
      height: 70%;
      position: absolute;
      left: 35%;
      bottom: 0;
    }
    .title-box {
      background-color: white;
      border-radius: 31px;
      font-size: $title-size;
      padding: 14px 38px 14px 38px;
      box-shadow: 14px 17px 6px rgb(0, 0, 0, 0.16);
      position: absolute;
      right: -40px;
      bottom: 100px;
    }
  }
}

.bottom-bar {
  width: 100%;
  background: transparent linear-gradient(90deg, #ff5af9 0%, #63b6ff 53%, #4bffff 100%) 0% 0% no-repeat padding-box;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
}

.loading-page {
  overflow: hidden;
  width: 100%;
  height: 100%;
  position: fixed;
  background-color: white;
  z-index: 20;
  align-items: center;
  justify-content: center;
  flex-direction: column;

  .title {
    font-size: 100px;
    font-weight: 400;
  }
  .label-shown {
    color: white;
    font-size: 10px;
    animation: fadeIn 0.5s;
  }
  .label-notshown {
    opacity: 0;
  }
}

.shown {
  opacity: 1;
}
.notshown {
  animation: fadeOut 1s;
  opacity: 0;
}

